import React, {useState} from 'react'

const recipes = [
       
    {id:1 ,  name:"Bánh mì", ingredients: ["Pork belly, fish cakes, meatballs, and the very necessary pickled carrots, on a French Baguette"], price:"$7.50"},
    {id:2, name:"Phở", ingredients: ["Assortment of meat, bean sprouts, lime wedges, basil, mint, cilantro, onions,chili sauce, & fish sauce "], price:"$9.50"},
    {id:3, name: "Bún chả", ingredients: [ "Grilled pork sausage patties, a basket of herbs, bean sprouts, pickled veggies, and, once again, the ever-important nước chấm sauce"], price : '$8.00 '},
    {id:4, name:"Ca Kho To (Caramelized Fish in Clay Pot)", ingredients: ["In this dish, the fish develops a sweet-savory gooeyness from the sugar and fish sauce over the course of the long braise"], price:"$7.50"},
    {id:5,  name:"Bánh xèo", ingredients: ["The batter is made with rice flour, coconut milk, and turmeric (hence the nice golden-yellow hue) and is pan-fried altogether with pork, shrimp, and a heap of bean sprouts. Wrap up the pancake with lettuce and herbs. "], price:"$13.50"},
    {id:6, name: "Bún chả", ingredients: [ "Made with starfruit, tamarind, pineapple, and/or tomatoes. They are full of contrasting flavors ,sour, sweet, and savory and contrasting textures ,various veggies and seafood"], price : '$7.50 '},
    {id:7 ,  name:"Bánh mì", ingredients: ["Pork belly, fish cakes, meatballs, and the very necessary pickled carrots, on a French Baguette"], price:"$7.50"},
    {id:8, name:"Phở", ingredients: ["Assortment of meat, bean sprouts, lime wedges, basil, mint, cilantro, onions,chili sauce, & fish sauce "], price:"$7.50"},
    {id:9, name: "Bún chả", ingredients: [ "Grilled pork sausage patties, a basket of herbs, bean sprouts, pickled veggies, and, once again, the ever-important nước chấm sauce"], price : '$7.50 '},
    {id:10, name:"Ca Kho To (Caramelized Fish in Clay Pot)", ingredients: ["In this dish, the fish develops a sweet-savory gooeyness from the sugar and fish sauce over the course of the long braise"], price:"$13.50"},
    {id:11, name:"Bánh xèo", ingredients: ["The batter is made with rice flour, coconut milk, and turmeric (hence the nice golden-yellow hue) and is pan-fried altogether with pork, shrimp, and a heap of bean sprouts. Wrap up the pancake with lettuce and herbs. "], price:"$7.50"},
    {id:12, name: "Bún chả", ingredients: [ "Made with starfruit, tamarind, pineapple, and/or tomatoes. They are full of contrasting flavors ,sour, sweet, and savory and contrasting textures ,various veggies and seafood"], price : '$7.50 '}
  ];


function Order() {


      const [food, setFood] = useState(recipes);
      const [buttonAdd, setButtonAdd] = useState(false);
      const [clickFood, setClickFood] = useState([]);
      const [clickPrice, setClickPrice] = useState([]);
      const [modal, setModal] = useState(false);
      const [order, setOrder] = useState([]);
      const [price, setPrice] = useState([]);
      let newPrice = 0;

      function itemClick(meal) {
        setModal(true)
        if (modal === true) {
          console.log(true )
        }

       setClickFood([meal.name, meal.price])
       const newOrder = order.concat( meal.name, meal.price );
       setOrder([...newOrder])     
      }
      const deleteItem = (order, id) => {
        setOrder(order.filter((order) => order.id !== id))
      }


      console.log(clickFood)
    return (
			<div className="Menu">
				<div></div>
				<h1 style={{ color: "red", position: "relative", right: "20vw" }}>
					Main Dishes
				</h1>

				<section className="appMenu">
					<h5 className="menugrid">
						{" "}
						{food.map((meal) => (
							<div className="menuItem" onClick={() => itemClick(meal)}>
								<h3 className="name" style={{ color: "red" }} key={meal.id}>
									{meal.name}
								</h3>
								<h4 key={meal.ingredients}>{meal.ingredients}</h4>
								<h4 key={[meal.price]}>{meal.price}</h4>
							</div>
						))}
					</h5>

					<section className="appOrder">
						<div className="orderContainer">
							<h1> Your Order</h1> 
             
              {order.map((order) => (
               
							<div buttonAdd  >
								<h3   key={[order]} style={{ color: "red" }}>
									{order} 
								</h3>
            
                <h3   key={[price]} style={{ color: "red" }}>
									{price}
								</h3>
                
           
								
							</div> 
						))} <h4  style={{color:'red'}} onClick={() => deleteItem(order)}>x</h4>
						</div>
					</section>

          <section className = "menuModal" style={{display: modal ? 'block' : 'none' }}>
                <div className= "modalContainer">
                
         <h1> {clickFood}   <span className="cancel" style={{fontSize:'2em', color:'red', cursor:'pointer'}}
                onClick={ () => setModal(false)}>x</span>   </h1>
                    <h4>Protien</h4>
                    <select style={{height:'5vh', width:'6vw'}} name="Protien" >
                      <option value="0">Beef</option>
                      <option value="1">Pork</option>
                      <option value="2">Chicken</option>
                      <option value="3">Tofu</option>
                   
                    </select>
                    <h4>Spice</h4>
                    <select style={{height:'5vh', width:'6vw'}} name="Protien" >
                      <option value="1">Mild</option>
                      <option value="2">Medium</option>
                      <option value="3">Hot</option>
                      <option value="4">Xtra Hot! </option>
                   
                    </select>
                    <textarea placeholder="Any additional information we should know. Allergies, dietary restrictions, etc"
                    
                        rows={5}
                        cols={5}
                      />
                   <button style={{backgroundColor:'red', color:'white'}} className="modalBut"    onClick={ () => 
                    setModal(false)
                    }>Add to cart</button>
                   

                </div>
             
          </section>
				</section>
			</div>
		);
}

export default Order